.loginPage {
  margin: var(--page-margin);
  font-size: 17px;
  color: black;
}

.loginPage-box {
  border: 1px lightgray solid;
  border-radius: 3px;
  margin-bottom: 50px;
  padding: 30px 5%;
  display: inline-block;
  color: #142239;
}
input {
  padding: 8px;
  border-radius: 3px;
  text-indent: 8px;
  border: 1px lightgray solid;
  width: 100%;
  font-size: 15px;
  box-sizing: border-box;
}
form {
  display: flex;
  flex-direction: column;
}
h2 {
  color: #002868;
  text-transform: uppercase;
}

.button-text-box-1 {
  text-align: center;
  color: white;
  letter-spacing: 1px;
  text-transform: uppercase;
  background-color: darkblue;
  color: white;
  border-radius: 3px;
  padding: 5px 8px;
  transition: 0.3s;
  width: 100%;
  font-size: 14px;
  box-sizing: border-box;
}

.button-text-box-1:hover {
  cursor: pointer;
  opacity: 0.8;
}
.forgotPassword {
  margin: 10px 0 30px 0;
}
.privacyPolicyText {
  font-size: 14px;
}

.inputsWidth > * {
  margin: 10px 0;
}
.forgotPasswordInstructions {
  font-size: 18px;
  margin: 30px 0;
}
